import React, { useEffect, useState } from 'react'
import { Link, useLocation } from 'react-router-dom';
import scaffolding from '../system/client';
const { api } = scaffolding;



const Home = () => {
  const [posts, setPosts] = useState([]);

  const [, category] = useLocation().search.split('=');

  useEffect(() => {
    const getPosts = async () => {
      try {
        await scaffolding.load('post');
        const param = category ? category : false;
        const res = await api.post.getPosts(param);
        setPosts(res.result);
      } catch (error) {
        console.log(error);
      }
    }
    getPosts();
  }, [category])


  const getText = (html) => {
    const doc = new DOMParser().parseFromString(html, "text/html")
    return doc.body.textContent
  }

  const truncate = (input) =>
    input?.length > 300 ? `${input.substring(0, 154)}...` : input;


  return (
    <div className='home'>
      <div className='posts'>
        {posts.map((post) => (
          <div className='post' key={post.postid}>
            <div className='img'>
              <img src={post.image} alt='' />
            </div>
            <div className='content'>
              <Link className='link' to={`/post/${post.postid}`}>
                <h1>{post.title}</h1>
              </Link>
              <div style={{ margin: '15px' }}>
                {truncate(getText(post.descr))}
              </div>
              <Link className='link' to={`/post/${post.postid}`}>
                <button>Read more</button>
              </Link>
            </div>
          </div>

        ))}
      </div>
    </div>
  )
}

export default Home